body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Header {
  border: 1px solid black;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  background-color: #32b5de; }
  .Header a {
    margin: 0 5px;
    color: white;
    text-decoration: none; }
    .Header a h1 {
      margin: 0;
      font-size: 22px;
      padding: 5px 20px; }
  .Header nav {
    margin-right: 10px; }
    .Header nav a {
      display: inline-block;
      margin-left: 20px; }
      .Header nav a:hover {
        color: #006edc; }

h2 {
  text-align: center; }

form {
  background-color: #F0F0F0;
  width: 300px;
  margin: 0 auto;
  padding-bottom: 5px; }
  form h3 {
    text-align: center;
    margin: 0;
    padding: 5px 0; }
  form p {
    margin: 0;
    text-align: center;
    padding-bottom: 5px; }
  form label {
    display: block;
    text-align: center;
    padding: 5px 0; }
  form input {
    display: block;
    margin: auto;
    width: 150px;
    padding: 2px;
    border: 1px solid #a0a096; }
  form input[type='submit'] {
    margin: 5px auto;
    width: 156px;
    -webkit-transition: background-color 0.5s;
    transition: background-color 0.5s; }
    form input[type='submit']:hover {
      background-color: #006edc;
      color: #FFFFFF; }

select {
  border-radius: 0;
  display: block;
  margin: auto;
  margin-bottom: 15px; }

.react-calendar {
  margin: auto; }

.react-select {
  border: 1px solid black;
  border-radius: 0;
  width: 400px; }

p {
  text-align: center; }

.client {
  width: 80%;
  margin: 5px auto;
  border: 1px solid #a0a096;
  padding: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center; }
  .client .bold {
    font-weight: bold; }
  .client .delete {
    color: red;
    cursor: pointer; }
    .client .delete:hover {
      color: #006edc; }
  .client:hover {
    background-color: #c7c7bd; }

.reservationList {
  background-color: #FFFFFF; }
  .reservationList h2 {
    text-align: center; }
  .reservationList .searchByName {
    margin-top: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center; }
  .reservationList .search-options {
    width: 40%;
    margin: 20px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around; }
    .reservationList .search-options button:hover, .reservationList .search-options button.active {
      background-color: #006edc;
      color: #FFFFFF; }

